.frame-child {
  top: 0;
  background-color: var(--color-gainsboro-100);
  border: 0.5px solid var(--color-black);
  box-sizing: border-box;
}
.frame-child,
.frame-item,
.price-high-,
.price-low- {
  position: absolute;
  left: 0;
  width: 112px;
  height: 24px;
}
.frame-item {
  top: 24px;
  background-color: var(--color-gainsboro-100);
  border: 0.5px solid var(--color-black);
  box-sizing: border-box;
}
.price-high-,
.price-low- {
  top: 0;
  display: inline-block;
}
.price-high- {
  top: 24px;
}
.rectangle-parent {
  position: relative;
  background-color: var(--color-white);
  border: 0.5px solid var(--color-black);
  box-sizing: border-box;
  width: 112px;
  height: 48px;
  overflow: hidden;
  max-width: 90%;
  max-height: 90%;
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--color-black);
  font-family: var(--font-inter);
}
